<template>
	<div class="home_view">
		<div class="projectTitle">欢迎使用 {{projectName}}</div>
		<div class="count_list">
			<el-collapse-transition v-if="btnAuth('kefangxinxi','首页总数')">
				<el-card v-show="countTypeList.closekefangxinxiCountType" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								客房信息
							</div>
							<div class="card_head_right">
								<el-icon @click="countTypeClick('hiddenkefangxinxiCountType')" class="showIcons"
									:class="countTypeList.hiddenkefangxinxiCountType?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="countTypeClick('closekefangxinxiCountType')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
							
						</div>
					</template>
					<el-collapse-transition>
						<div class="count_item" v-show="countTypeList.hiddenkefangxinxiCountType">
							<div class="count_title">客房信息总数</div>
							<div class="count_num">{{kefangxinxiCount}}</div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
		</div>
		<div class="card_list">
			<el-collapse-transition v-if="btnAuth('kefangyuding','首页统计')">
				<el-card v-show="cardTypeList.closekefangyudingChartType1" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								客房预订
							</div>
							<div class="card_head_right">
								<el-icon @click="cardTypeClick('hiddenkefangyudingChartType1')" class="showIcons"
									:class="cardTypeList.hiddenkefangyudingChartType1?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="cardTypeClick('closekefangyudingChartType1')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
						</div>
					</template>
					<el-collapse-transition>
						<div class="card_item" v-show="cardTypeList.hiddenkefangyudingChartType1">
							<div id="kefangyudingyudingzhuangtaiEchart1" style="width: 100%;height: 400px;"></div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
			<el-collapse-transition v-if="btnAuth('kefangruzhu','首页统计')">
				<el-card v-show="cardTypeList.closekefangruzhuChartType1" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								客房入住
							</div>
							<div class="card_head_right">
								<el-icon @click="cardTypeClick('hiddenkefangruzhuChartType1')" class="showIcons"
									:class="cardTypeList.hiddenkefangruzhuChartType1?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="cardTypeClick('closekefangruzhuChartType1')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
						</div>
					</template>
					<el-collapse-transition>
						<div class="card_item" v-show="cardTypeList.hiddenkefangruzhuChartType1">
							<div id="kefangruzhuyudingjianshuEchart1" style="width: 100%;height: 400px;"></div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
			<el-collapse-transition v-if="btnAuth('tuifangjiesuan','首页统计')">
				<el-card v-show="cardTypeList.closetuifangjiesuanChartType1" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								退房结算
							</div>
							<div class="card_head_right">
								<el-icon @click="cardTypeClick('hiddentuifangjiesuanChartType1')" class="showIcons"
									:class="cardTypeList.hiddentuifangjiesuanChartType1?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="cardTypeClick('closetuifangjiesuanChartType1')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
						</div>
					</template>
					<el-collapse-transition>
						<div class="card_item" v-show="cardTypeList.hiddentuifangjiesuanChartType1">
							<div id="tuifangjiesuanzhifujineEchart1" style="width: 100%;height: 400px;"></div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
		</div>
	</div>
</template>

<script setup>
	import {
		inject,
		nextTick,
		ref,
		getCurrentInstance
	} from 'vue';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const projectName = context.$project.projectName
	const countTypeList = ref({
	})
	const getCountList=()=>{
		countTypeList.value.closekefangxinxiCountType = true
		countTypeList.value.hiddenkefangxinxiCountType = true
		if(btnAuth('kefangxinxi','首页总数')){
			getkefangxinxiCount()
		}
	}
	const kefangxinxiCount = ref(0)
	const getkefangxinxiCount = () => {
		context?.$http({
			url:'kefangxinxi/count',
			method: 'get'
		}).then(res=>{
			kefangxinxiCount.value = res.data.data
		})
	}
	const countTypeClick = (e) => {
		countTypeList.value[e] = !countTypeList.value[e]
	}
	const init=()=>{
		getCountList()
		getCardList()
	}
	//权限验证
	const btnAuth = (e,a)=>{
		return context?.$toolUtil.isAuth(e,a)
	}
	let echarts = inject("echarts")
	const cardTypeClick = (e) =>{
		cardTypeList.value[e] = !cardTypeList.value[e]
		setTimeout(()=>{
			getCardList()
		},1000)
	}
	const cardTypeList = ref({
		closekefangyudingChartType1: true,
		hiddenkefangyudingChartType1: true,
		closekefangruzhuChartType1: true,
		hiddenkefangruzhuChartType1: true,
		closetuifangjiesuanChartType1: true,
		hiddentuifangjiesuanChartType1: true,
	})
	const getCardList = () => {
		if(btnAuth('kefangyuding','首页统计')){
			getkefangyudingChart1()
		}
		if(btnAuth('kefangruzhu','首页统计')){
			getkefangruzhuChart1()
		}
		if(btnAuth('tuifangjiesuan','首页统计')){
			gettuifangjiesuanChart1()
		}
	}
	const getkefangyudingChart1 = () => {
		nextTick(()=>{
			var yudingzhuangtaiEchart1 = echarts.init(document.getElementById("kefangyudingyudingzhuangtaiEchart1"),'macarons');
			context?.$http({
				url: "kefangyuding/typeStat/yudingzhuangtai",
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis = [];
				let pArray = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].yudingzhuangtai);
				    yAxis.push(parseFloat((res[i].total)));
				    pArray.push({
				        value: parseFloat((res[i].total)),
				        name: res[i].yudingzhuangtai
				    })
				}
				var option = {};
                option = {
                    title: {
                        text: '房间预订状态数据统计',
                        left: 'center'
                    },
                    tooltip: {
                      trigger: 'item',
                      formatter: '{b} : {c}'
                    },
                    xAxis: {
                        type: 'category',
                        data: xAxis,
                        axisLabel: {
                            rotate: 40
                        }
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: yAxis,
                        type: 'bar'
                    }]
                };
				yudingzhuangtaiEchart1.clear()
				// 使用刚指定的配置项和数据显示图表。
				yudingzhuangtaiEchart1.setOption(option);
				//根据窗口的大小变动图表
				yudingzhuangtaiEchart1.resize();
			})
		})
	}
	const getkefangruzhuChart1 = () => {
		nextTick(()=>{
			var yudingjianshuEchart1 = echarts.init(document.getElementById("kefangruzhuyudingjianshuEchart1"),'macarons');
			context?.$http({
				url: `kefangruzhu/value/fangjianleixing/yudingjianshu`,
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis = [];
				let pArray = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].fangjianleixing);
				    yAxis.push(parseFloat((res[i].total)));
				    pArray.push({
				        value: parseFloat((res[i].total)),
				        name: res[i].fangjianleixing
				    })
				}
				var option = {};
                option = {
                    title: {
                        text: '房间入住类型数据统计',
                        left: 'center'
                    },
                    legend: {
                        orient: 'vertical',
                        left: 'left'
                    },
                    tooltip: {
						trigger: 'item',
						formatter: '{b} : {c} ({d}%)'
                    },
                    series: [
                        {
                            type: 'pie',
                            radius: '55%',
                            center: ['50%', '60%'],
                            data: pArray,
                            emphasis: {
                                itemStyle: {
                                    shadowBlur: 10,
                                    shadowOffsetX: 0,
									shadowColor: 'rgba(0, 0, 0, 0.5)'
                                }
                            }
                        }
                    ]
                };
				yudingjianshuEchart1.clear()
				// 使用刚指定的配置项和数据显示图表。
				yudingjianshuEchart1.setOption(option);
				//根据窗口的大小变动图表
				yudingjianshuEchart1.resize();
			})
		})
	}
	const gettuifangjiesuanChart1 = () => {
		nextTick(()=>{
			var zhifujineEchart1 = echarts.init(document.getElementById("tuifangjiesuanzhifujineEchart1"),'macarons');
			context?.$http({
				url: `tuifangjiesuan/value/tuifangshijian/zhifujine/月`,
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis = [];
				let pArray = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].tuifangshijian);
				    yAxis.push(parseFloat((res[i].total)));
				    pArray.push({
				        value: parseFloat((res[i].total)),
				        name: res[i].tuifangshijian
				    })
				}
				var option = {};
                option = {
                    title: {
                        text: '月营业额统计',
                        left: 'center'
                    },
                    tooltip: {
                      trigger: 'item',
                      formatter: '{b} : {c}'
                    },
                    xAxis: {
                        type: 'category',
                        data: xAxis,
                        axisLabel: {
                            rotate: 40
                        }
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: yAxis,
                        type: 'bar'
                    }]
                };
				zhifujineEchart1.clear()
				// 使用刚指定的配置项和数据显示图表。
				zhifujineEchart1.setOption(option);
				//根据窗口的大小变动图表
				zhifujineEchart1.resize();
			})
		})
	}
	init()
</script>
<style lang="scss">
	.projectTitle{
		padding: 20px 0;
		font-weight: bold;
		display: flex;
		width: 100%;
		font-size: 30px;
		justify-content: center;
		align-items: center;
		height: 50%;
	}

	.showIcons {
		transition: transform 0.3s;
		margin-right: 10px;
	}

	.showIcons1 {
		transform: rotate(-180deg);
	}
	
	// 总数盒子
	.count_list{
		padding: 0 0 20px;
		display: flex;
		width: 100%;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		// 总数card
		.card_view {
			border: 0px solid #e4e7ed;
			border-radius: 10px;
			box-shadow: none;
			margin: 0 10px 10px;
			flex: 1;
			background: linear-gradient(90deg, #E3EDF9 0%, #D9E6F7 100%);
			width: 100%;
			position: relative;
			box-sizing: border-box;
			height: auto;
			// card头部
			.el-card__header {
				border: 1px solid #e4e7ed;
				padding: 7px 20px;
				left: 0;
				background: #62B8FF;
				bottom: 0;
				width: 100%;
				border-width: 0;
				position: absolute;
				// 头部盒子
				.index_card_head {
					display: flex;
					width: 100%;
					justify-content: space-between;
					align-items: center;
					// 标题
					.card_head_title {
						color: #868686;
						font-weight: bold;
						font-size: 14px;
					}
					// 按钮盒子
					.card_head_right {
						display: flex;
						align-items: center;
						// 按钮
						.el-icon {
							cursor: pointer;
							color: #868686;
							font-weight: bold;
							font-size: 12px;
						}
					}
				}
			}
			// body
			.el-card__body {
				padding: 0;
				// body盒子
				.count_item{
					padding: 30px 20px 60px;
					flex-direction: column-reverse;
					display: flex;
					// 总数标题
					.count_title{
						color: #3753A2;
						font-weight: bold;
						font-size: 16px;
						line-height: 1;
						order: 1;
					}
					// 总数数字
					.count_num{
						color: #3753A2;
						font-weight: bold;
						font-size: 50px;
						line-height: 1.5;
						order: 2;
					}
				}
			}
		}
	}
	// 首页盒子
	.home_view {
		padding: 20px;
		background: url(http://clfile.zggen.cn/20240203/09a98f4852104a599055d47953ef0a23.png) no-repeat center top / 100% 100% !important;
		width: 100%;
		min-height: 100vh;
		height: auto;
	}
	// 统计图盒子
	.card_list {
		padding: 0 0 20px;
		display: flex;
		width: 100%;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		// 统计图card
		.card_view {
			border: 0px solid #e4e7ed;
			border-radius: 10px;
			box-shadow: none;
			margin: 0 10px 10px;
			flex: 1;
			background: linear-gradient(90deg, #E3EDF9 0%, #D9E6F7 100%);
			width: 100%;
			position: relative;
			box-sizing: border-box;
			height: auto;
			// 头部
			.el-card__header {
				border: 0px solid #e4e7ed;
				padding: 7px 20px;
				left: 0;
				bottom: 0;
				background: #62B8FF;
				width: 100%;
				border-width: 0;
				position: absolute;
				// 头部盒子
				.index_card_head {
					display: flex;
					width: 100%;
					justify-content: space-between;
					align-items: center;
					// 标题
					.card_head_title {
						color: #868686;
						font-weight: bold;
						font-size: 14px;
					}
					// 按钮盒子
					.card_head_right {
						display: flex;
						align-items: center;
						// 按钮
						.el-icon{
							cursor: pointer;
							color: #868686;
							font-weight: bold;
							font-size: 12px;
						}
					}
				}
			}
			// body
			.el-card__body {
				padding: 0;
				// body盒子
				.card_item{
					padding: 30px;
					text-align: center;
				}
			}
		}
	}
</style>
